import React from 'react'
import type { ForwardRefComponent as PolymorphicForwardRefComponent } from '~/utils/polymorphic'
import type { PrimitiveProps } from '../../types'
import { getformattedProps } from '../../../../utils/primitives'
import { styleProps, StyledSpan } from './styles'

type PolymorphicText = PolymorphicForwardRefComponent<'span', PrimitiveProps<typeof styleProps>>

type FontFamilyType = Record<string, string>

const Text = React.forwardRef(
  ({ as = 'span', css, fontFamily = 'primary', children, ...props }, forwardedRef) => {
    console.log({ fontFamily })
    const { mediaStyles, restProps } = getformattedProps({
      props,
      styleProps: { ...styleProps, fontFamily: fontFamily as FontFamilyType }
    })

    return (
      <StyledSpan as={as} css={{ ...mediaStyles, ...css }} ref={forwardedRef} {...restProps}>
        {children}
      </StyledSpan>
    )
  }
) as PolymorphicText

Text.displayName = 'Text'

export { Text }
