import { styled, config } from '../../../../stitches.config'
import { getStylePropsFromToken } from '../../../../styles'

const {
  theme: { colors, fontSizes, fonts }
} = config

type Colors = typeof colors
type FontFamilies = typeof fonts
type FontSizes = typeof fontSizes

export const styleAliases = {
  fontColor: 'color',
  align: 'textAlign'
}

const fontColor = getStylePropsFromToken<Colors>(colors)
const fontFamily = getStylePropsFromToken<FontFamilies>(fonts)
export const fontSize = getStylePropsFromToken<FontSizes>(fontSizes)

const fontWeight = {
  normal: '400',
  bold: '700'
}

const align = {
  left: 'left',
  center: 'center',
  right: 'right',
  justify: 'justify',
  start: 'start',
  end: 'end'
}

const textTransform = {
  uppercase: 'uppercase',
  lowercase: 'lowercase',
  capitalize: 'capitalize',
  normalCase: 'none'
}

export const styleProps = {
  fontColor,
  fontFamily,
  fontSize,
  fontWeight,
  align,
  textTransform
} as const

export const StyledSpan = styled('span')
